                        <p className="lineFirst">3. 用户应当妥善的保存自己的用户名和密码，不得以任何形式擅自转让或授权他人使用自己在融老板的用户账号。用户对其在融老板注册的用户名和密码的安全性负全部责任，并对以其用户名进行的所有活动和事件负全部责任，用户若发现任何非法使用其账号或存在安全漏洞的情况，应立即通知融老板。</p>
