                        <p className="lineFirst">3. 为了更好的服务用户，融老板及其关联机构除对用户提供的资料（包括但不限于文字、图片、视频）拥有合理使用权外，还拥有独家的、全球通用的、免费的许可使用权。许可使用范围包括复制、发行、展览、信息网络传播、改编、翻译、汇编等。用户将前述资料提供给融老板期间，使用权持续有效。若用户发现融老板对用户资料的使用不当时，请及时提出，融老板将尽快改正。</p>
