                        <p className="lineFirst">9. 用户完全理解并同意，通过融老板可能会收到推广信息及服务信息，凡因接收相应推广信息而与信息发布者或推广商发生交易行为而遭受的任何性质的损失或损害，融老板均不予负责。</p>
