                        <p className="lineFirst">2) 若通知以电子邮件形式发送，则通知收件日期以邮件进入收件方指定的电子邮件系统的时间为准；若通知及函件以EMS邮政快递或挂号邮件发送时，则通知收件日期以收件方签收之日为准；以多种方式通知收件方的，收件日期以最早的收件日期为准。</p>
